.container {
    background: #EBECF0;
    border-radius: 10px;
    max-width: 282px;
    min-width: 200px;
}

.container__title {
    margin: 12px 0 15px 12px;
    text-align: start;
}

.container__task {
    margin: 15px 12px;
    max-width: 241px;
    min-width: 159px;
    background: #FFFFFF;
    border-radius: 5px;
    padding: 7px 9px 7px 8px;
}

.container__btn {
    margin: 15px 12px;
    cursor: pointer;
    background-color: transparent;
    border: none;
    text-align: start;
    color: #5E6C84;
    display: flex;
    align-items: center;
    gap: 4px;
    padding: 0;
    font-size: 16px;
}